@import "../../Global/_Styles/Helpers/Mixins/MediaQueries.css";
@import "../../Global/_Styles/Helpers/Mixins/Forms.css";
@import "../../Global/_Styles/Config/Typo.css";
@import "../../Global/_Styles/Config/Misc.css";
@import "../../Global/_Styles/Config/Fonts.css";
@import "../../Global/_Styles/Config/Dimensions.css";
@import "../../Global/_Styles/Config/Colors.css";
@import "../../Global/_Styles/Config/Breakpoints.css";
.nueww-input-singleline {
  @mixin input-groups;

  &__label {
    @mixin labels;
  }

  &__label--is-hidden {
    @mixin labels--hidden;
  }

  &__input {
    @mixin inputs;
  }

  &__label,
  &__input {
    display: inline-block;
    vertical-align: top;
  }

  &__required {
    vertical-align: top;
  }
}
